import React from "react";
import { Helmet } from "react-helmet";
import "./back.less";

const Back = (props: { Context: any }) => {
  let { Context } = props;
  return (
    <div id="back">
      <Helmet>
        <title>Switchの晚安短信~</title>
      </Helmet>
      <div id="main">
        <Context />
      </div>
    </div>
  );
};

export default Back;
