import React from "react";
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";
import NotFound from "./days/day-1/notFound";
import Day0Context from "./days/day0/day0";

const MainRouter = () => {
  return (
    <BrowserRouter>
      <Switch>
        <Route exact path="/" component={NotFound} />
        <Route exact path="/day0" component={Day0Context} />
        <Route render={(props: any) => <Redirect to="/" />} />
      </Switch>
    </BrowserRouter>
  );
};

export default MainRouter;
