import { Tooltip } from "antd";
import React from "react";

const Hid = (props: { content: any; tip?: string }) => {
  let { content, tip } = props;
  return (
    <>
      <Tooltip className="hid" title={tip ?? "你知道的太多了~"}>
        {content}
      </Tooltip>
    </>
  );
};
export default Hid;
